@import url(https://fonts.googleapis.com/css?family=Montserrat:700|Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    font-size: 16px;
    background: white;
    font-family: 'Montserrat', sans-serif;
    overflow-x: hidden;
    margin: 0;
    padding: 0;
    z-index: 1;
}


.App {
  text-align: center;
}



.App-header {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
}
.heading-background {
    position: fixed;
    top: 0;
    left: 0;
    min-height: 100%;
    min-width: 100%;
    opacity: 0.4;
    z-index: -100;
}
header{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
header h1{
font-size: 1em;
font-weight: 400;  
padding: 2em;
margin-right: 50em;
}
nav{
  position:fixed;
  left:60em;
  top:6em;
z-index: 100;
}
nav li{
  display: inline-block;
  cursor: pointer;

}
nav li .link{
  text-decoration: none;
  color: red;
  padding:1em;
  /*opacity: 0.5;*/
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}
li .link:hover{
  opacity: 1;
}
.active{
  color:black;
  opacity: 1;
}

.header-title button{
  background: black;
  border: 0px;
  padding: 1em 3em;
  transition: all 0.2s ease-out;
  -webkit-transition: all 0.2s ease-out;
  -moz-transition: all 0.2s ease-out;
  -ms-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
}
.header-title button:hover{
  opacity: 0.8;
}
.header-title button a{
  font-family: 'Open Sans', sans-serif;
  color:white;
  text-decoration: none;
}
.header-title{
  position: relative;
  font-family: 'Montserrat', sans-serif;
  font-size: 3em;
  text-align: left;
  top:2em;
}
.about{
  display: flex;
  flex-direction: row;
  margin-top: 10em;
}
.about-content{
  position: relative;
  
}
.about-content h1{
  font-family: 'Montserrat', sans-serif;
  font-size: 4em;
  text-align: center;
  
}
.about-content p {
    padding-left: 10em;
    padding-right: 10em;
    letter-spacing: 0.3em;
    opacity: 0.8;
    padding-top: 4em;
}
/*.about-content p{
  font-size: 1.3em;
  text-align: center;
  width: 37em;
}*/
.about img{
  margin-left: 5em;
}
/*.work{
  position: relative;
  display: flex;
  flex-direction: column;
  left:15em;
  top:8em;
  align-items: flex-start;
}*/
.work {
    margin-top: 10em;
}

.work .heading{
  font-family: 'Montserrat', sans-serif;
  font-size: 4em;
  text-align: center;
 
}
.work .work-content{
  display: grid;
  grid-template-columns: repeat(2,1fr);
  grid-gap: 0.3em;
}

.project img{
width: 30em;  
height: 30em;
}
.contact{
  position: relative;
  top:10em;
  
  text-align: center;
}

.contact p {
    text-align: center;
    letter-spacing: 0.2em;
    line-height: 2em;
}
.contact h1{
    font-family: 'Montserrat', sans-serif;
  font-size: 4em;
  text-align: center;
 
}
.amazing-color{
  color:#9352ae;
  opacity: 1;
}

.contact .email{
  color:black;
  font-size: 2em;
  text-decoration: none;
  text-align: center;
}
.contact li{
  display: inline-block;
  margin-top:4em;
  margin-bottom: 3em;
  margin-right: 1em;
  margin-left: 0;
}
.contact li a{
  text-decoration: none;
  color:black;
}
.footer{
  position: relative;
  top:2em;
  padding: 1em;
}
.footer a{
  text-decoration: none;
  color:#2c59fb;
}

@media screen and (max-device-width: 600px) {
 
    .heading-background {
       overflow-x: hidden;
    }
    .App-header {
        background-color: black;
       
    }
    .header-title button {
        display: none;
        overflow-x: hidden;
        max-width: 100%;
    }
    .part {
        
        display: none;
    }
  
    .header-title {
        font-size: 0.8em;
        text-align: center;
        padding-top: 10em;
        line-height: 3em;
    
    }


    .work .work-content {
        
        grid-template-columns: repeat(1,1fr);
        grid-gap: 0.3em;
    }


    .about-content h1,
    .work .heading,
    .contact h1 {
        font-size: 2em;
        text-align: center;

    }
    .about-content p {
        font-size: 0.7em;
        
    }
    .contact .email {
        font-size: 0.7em;
    }
}



